<template>
    <div class="welcomeBox">
        <h1>{{ msg }}</h1>
        <h3>This is my SPA demo by using vue-router.</h3>
    </div>
</template>

<script>
export default {
    name: 'HelloWorld',
    data () {
        return {
        msg: 'Welcome!'
        }
    }
}
</script>

<style lang="scss">
.welcomeBox{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 800px;
    margin: auto;
    height: 30vh;
}
</style>



